<template>
  <div>
    <v-container>
        <v-row justify="center" class="pb-5">
          <v-btn
            x-large
            color="error"
            class="mr-5"
            @click="role = 0"
            dark
          >SOY PASAJERO</v-btn>
          <v-btn
            x-large
            color="error"
            class="ml-5"
            @click="role = 1"
            dark
          >SOY PILOTO</v-btn>
        </v-row>
      <form v-show="role === 0">
        <h1 class="display-2 pb-5">Nuevo Viaje</h1>
        <v-select
          v-model="source"
          :items="aerodromos"
          item-text="name"
          :error-messages="selectErrors"
          label="Origen"
          required
        ></v-select>
        <v-select
          v-model="target"
          :items="aerodromos"
          item-text="name"
          :error-messages="selectErrors"
          label="Destino"
          required
        ></v-select>
        <v-text-field label="Nro. de Pasajeros" v-model="pasajeros" type="number"></v-text-field>
        <v-row justify="center">
          <v-date-picker class="ma-4" v-model="date"></v-date-picker>
          <v-time-picker class="ma-4" v-model="time"></v-time-picker>
        </v-row>
        <v-row justify="center">
          <v-btn x-large color="success" @click="submit">Listo</v-btn>
        </v-row>
      </form>
    </v-container>
    <v-container>
      <v-data-iterator
        :items="requests"
        :items-per-page.sync="itemsPerPage"
        :footer-props="{ itemsPerPageOptions }"
      >
        <template v-slot:default="props">
          <v-row>
            <v-col v-for="(request, index) in requests" :key="request.id" cols="12" md="4">
              <v-card>
                <v-card-title>
                  <h4>Viaje: #{{request.id}}</h4>
                </v-card-title>
                <v-divider></v-divider>
                <v-list dense>
                  <v-list-item>
                    <v-list-item-content>Origen:</v-list-item-content>
                    <v-list-item-content class="align-end">{{ request.source }}</v-list-item-content>
                  </v-list-item>
                  <v-list-item>
                    <v-list-item-content>Destino:</v-list-item-content>
                    <v-list-item-content class="align-end">{{ request.target }}</v-list-item-content>
                  </v-list-item>
                  <v-list-item>
                    <v-list-item-content>Dia:</v-list-item-content>
                    <v-list-item-content class="align-end">{{ request.date }}</v-list-item-content>
                  </v-list-item>
                  <v-list-item>
                    <v-list-item-content>Hora:</v-list-item-content>
                    <v-list-item-content class="align-end">{{ request.time }}</v-list-item-content>
                  </v-list-item>
                  <v-list-item>
                    <v-list-item-content>Personas:</v-list-item-content>
                    <v-list-item-content class="align-end">{{ request.pasajeros }}</v-list-item-content>
                  </v-list-item>
                  <v-list-item>
                    <v-list-item-content>Estado:</v-list-item-content>
                    <v-list-item-content class="align-end">
                      <v-chip :color="request.status === 'En Espera' ? 'orange' : 'green'">{{request.status}}</v-chip>
                    </v-list-item-content>
                  </v-list-item>
                </v-list>
                <v-card-actions>
                  <v-btn v-show="role === 0" text color="red" block @click="cancelRequest(index)">Cancelar Solicitud</v-btn>
                  <v-btn v-show="role === 1" text color="success" block @click="pick(index)">Tomar Vuelo</v-btn>
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </template>
      </v-data-iterator>
    </v-container>
  </div>
</template>
